@mixin multi-text-overflow($line : 2) {
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: $line; /*  */
	-webkit-box-orient: vertical;
	word-wrap:break-word;
}
@mixin text-overflow(){
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}